.form__input-error {
  display: block;
  font-family: 'Inter', Arial, Helvetica, sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 12px;
  line-height: 15px;
  color: #FF0000;
  padding: 0;
  margin: 5px 0 0 0;
  min-height: 30px;
  opacity: 0;
  transition: opacity 0.3s linear;
}
